import requestInterceptor from '@/shared/requestInterceptor'
import { API } from '@/shared/config'
import { parseAxiosErrorToAppError } from '../shared/errorHelper'

async function addLike (currentTrailId, currentUserId) {
  try {
    await requestInterceptor.post(`${API}/likes`, {
      userId: currentUserId,
      trailId: currentTrailId
    })
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function removeLike (likeId) {
  try {
    await requestInterceptor.delete(`${API}/likes/${likeId}`)
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getCurrentUserLikes (currentUserId) {
  try {
    const response = await requestInterceptor.get(`${API}/users/${currentUserId}/likes`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const likesService = {
  addLike,
  removeLike,
  getCurrentUserLikes
}
