<template>
    <div>
        <select class="form-select" size="6" aria-label="size 6 select" v-model="trail">
            <option
            v-for="trail in trails"
            v-bind:key="trail.id"
            v-bind:value="trail"
            >
                {{ trail.name }}
            </option>
        </select>
    </div>
</template>

<script>
export default {
  data () {
    return {
      trail: {}
    }
  },
  computed: {
    trails () {
      if (this.$store.state.parks.currentParkSelected) {
        const trails = this.$store.state.parks.trailList
        return trails.sort(function (a, b) {
          return a.name.localeCompare(b.name)
        })
      }
      return []
    }
  },
  watch: {
    trail (newTrail, oldTrail) {
      this.$store.dispatch('trails/changeCurrentTrail', newTrail)
      this.$store.dispatch('likes/getNbLikesCurrentTrail', newTrail.id)
    }
  }
}
</script>

