import { likesService } from '../../services/likesService'
import { trailsService } from '../../services/trailsService'

const state = {
  isLiked: false,
  currentUserLikeList: [],
  currentNbLikes: 0,
  onError: false
}

const getters = {
}

const mutations = {
  setCurrentNbLikes (state, nbLikes) {
    state.currentNbLikes = nbLikes
  },
  setCurrentUserLikeList (state, currentUserLikeList) {
    state.currentUserLikeList = currentUserLikeList
  },
  setIsLiked (state, isLiked) {
    state.isLiked = isLiked
  }
}

const actions = {
  async getNbLikesCurrentTrail ({ commit, dispatch }, trailId) {
    const nbLikes = await trailsService.getLikesFor(trailId)
    dispatch('getIsLiked', trailId)
    commit('setCurrentNbLikes', nbLikes)
  },
  async getCurrentUserLikesList ({ commit }, userId) {
    const userLikes = await likesService.getCurrentUserLikes(userId)
    await commit('setCurrentUserLikeList', userLikes)
  },
  getIsLiked ({ state, commit }, trailId) {
    commit('setIsLiked', false)
    state.currentUserLikeList.forEach(like => {
      if (like.trailId === trailId) {
        commit('setIsLiked', true)
      }
    })
  },
  async addLike ({ dispatch }, likeInfos) {
    await likesService.addLike(likeInfos.trailId, likeInfos.userId)
    dispatch('getIsLiked', likeInfos.trailId)
  },
  async removeLike ({ commit }, likeId) {
    likesService.removeLike(likeId)
    commit('setIsLiked', false)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
