import { trailsService } from '../../services/trailsService'

const state = {
  currentTrail: {},
  currentTrailSelected: false,
  segments: [],
  onError: false,
  currentUserLikesList: []
}

const getters = {
  getTrailIsLiked: state => trailId => {
    // si ca plante ; essaye f minuscule
    if (state.currentUserLikesList.Find(t => t.trailId === trailId)) {
      return true
    } else {
      return false
    }
  },
  getLikeToBeDeleted: state => trailId => {
    return state.currentUserLikesList.Find(t => t.trailId === trailId)
  }
}

const mutations = {
  setCurrentTrail (state, trail) {
    state.currentTrail = trail
    state.currentTrailSelected = true
  },
  setSegments (state, segments) {
    state.segments = segments
  },
  setOnError (state) {
    state.onError = true
  }
}

const actions = {
  changeCurrentTrail ({ commit, dispatch }, trail) {
    commit('setCurrentTrail', trail)
    dispatch('getSegmentsForCurrentTrail')
  },
  async getSegmentsForCurrentTrail ({ commit }) {
    const response = []
    const segments = state.currentTrail.segments
    for (const segment of segments) {
      response.push(await trailsService.getSegment(segment))
    }
    commit('setSegments', response)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
