import { parksService } from '../../services/parksService'

const state = {
  parkList: [],
  trailList: [],
  currentPark: {},
  currentParkSelected: false,
  onError: false
}

const getters = {}

const mutations = {
  initializeParks (state, parks) {
    state.parkList = parks
  },
  setOnError (state) {
    state.onError = true
  },
  setCurrentPark (state, currentPark) {
    state.currentPark = currentPark
    state.currentParkSelected = true
  },
  initializeTrailList (state, trails) {
    state.trailList = trails
  }
}

const actions = {
  async getParks ({ commit }) {
    try {
      const response = await parksService.getParks()
      commit('initializeParks', response)
    } catch (error) {
      commit('setOnError')
    }
  },
  changeCurrentPark ({ commit, dispatch }, currentPark) {
    commit('setCurrentPark', currentPark)
    dispatch('getTrailsForCurrentPark')
  },
  async getTrailsForCurrentPark ({ commit }) {
    try {
      const response = await parksService.getTrailsForPark(state.currentPark.id)
      commit('initializeTrailList', response)
    } catch (error) {
      commit('setOnError')
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
