import * as axios from 'axios'
import { API } from '@/shared/config'
import { parseAxiosErrorToAppError } from '../shared/errorHelper'

async function getParks () {
  const response = await axios.get(`${API}/api/parks`)
  return response.data
}

async function getTrailsForPark (parkId) {
  try {
    const response = await axios.get(`${API}/api/parks/${parkId}/trails`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const parksService = {
  getParks,
  getTrailsForPark
}
