<template>
  <div>
   <button class="btn btn-primary"
    v-if="!isLiked" @click="saveLike()">
      <b-icon icon="heart"></b-icon>
    </button>
    <button class="btn btn-primary"
    v-else @click="deleteLike()">
      <b-icon icon="heart-fill"></b-icon>
    </button>
    <p class="trailName">{{ trailName }}</p>
    <p class="parkName">{{ parkName }}</p>
    <p class="nbLike">{{ nbLikes }}</p>
  </div>
</template>

<script>

export default {
  methods: {
    async saveLike () {
      try {
        const userId = this.currentUserId
        this.$store.dispatch('likes/addLike', { trailId: this.trailId, userId: userId })
        this.getLikes()
        this.updateUserLikesList()
      } catch (error) {
        alert("Erreur dans l'ajout du like dans la base de données")
      }
    },

    async deleteLike () {
      try {
        this.$store.dispatch('likes/removeLike', this.likeIdOfTrail)
        this.getLikes()
        this.updateUserLikesList()
      } catch (error) {
        alert('Erreur dans la suppression du like dans la base de données')
      }
    },
    getLikes () {
      this.$store.dispatch('likes/getNbLikesCurrentTrail', this.trailId)
    },
    async updateUserLikesList () {
      if (this.$store.getters['authentication/isLoggedIn']) {
        await this.$store.dispatch('likes/getCurrentUserLikesList', this.currentUserId)
      }
    }
  },
  computed: {
    trailName () {
      return this.$store.state.trails.currentTrail.name
    },
    trailId () {
      return this.$store.state.trails.currentTrail.id
    },
    parkName () {
      return this.$store.state.parks.currentPark.name
    },
    nbLikes () {
      return this.$store.state.likes.currentNbLikes
    },
    isLiked () {
      this.updateUserLikesList()
      return this.$store.state.likes.isLiked
    },
    currentUserId () {
      return Number(this.$store.getters['authentication/getTokenUserId'])
    },
    likeIdOfTrail () {
      return this.$store.state.likes.currentUserLikeList.find(like => like.trailId === this.trailId).id
    }
  },
  created () {
    this.updateUserLikesList()
  }
}
</script>
