import * as axios from 'axios'
import { API } from '@/shared/config'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'

async function getTrail (trailId) {
  try {
    const response = await axios.get(`${API}/trails/${trailId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getSegment (segmentId) {
  try {
    const response = await axios.get(`${API}/api/segments/${segmentId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getLikesFor (trailId) {
  try {
    const response = await axios.get(`${API}/api/trails/${trailId}/likes`)
    return Object.keys(response.data).length
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const trailsService = {
  getTrail,
  getSegment,
  getLikesFor
}
