<template>
    <div>
        <div class="row">
            <div class="col-3">
                <select class="form-control" v-model="park" id="parkSelect">
                    <option
                    v-for="park in parks"
                    v-bind:key="park.id"
                    v-bind:value="park"
                    >
                        {{ park.name }}
                    </option>
                </select>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      park: {}
    }
  },
  async created () {
    await this.$store.dispatch('parks/getParks')
  },
  watch: {
    park (newPark, oldPark) {
      this.$store.dispatch('parks/changeCurrentPark', newPark)
    }
  },
  computed: {
    parks () {
      const parkList = this.$store.state.parks.parkList
      return parkList.sort(function (a, b) {
        return a.name.localeCompare(b.name)
      })
    }
  }
}
</script>

