<template>
    <div>
        <l-map style="height: 500px; width: 600px" :zoom="zoom" :center="center">
            <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
            <l-polyline
              v-for="segment in segments"
              v-bind:key="segment.id"
             :lat-lngs="segment.coordinates" :color="getDifficultyColor(segment.level)"></l-polyline>
        </l-map>
    </div>
</template>

<script>
import { LMap, LTileLayer, LPolyline } from 'vue2-leaflet'

export default {
  components: {
    LMap,
    LTileLayer,
    LPolyline
  },
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
      '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 6,
      center: [47.20309337289463, -71.38208892322615]
    }
  },
  computed: {
    segments () {
      return this.$store.state.trails.segments
    }
  },
  methods: {
    getDifficultyColor (difficulty) {
      this.changCenter()
      this.changeZoom()
      switch (`${difficulty}`) {
        case 'Facile':
          return 'green'
        case 'Difficile':
          return 'orange'
        case 'Intermédiaire':
          return 'blue'
        case 'Très difficile':
          return 'red'
        default:
          return 'black'
      }
    },
    changCenter () {
      this.center = this.segments[0].coordinates[0]
    },
    changeZoom () {
      this.zoom = 15
    }
  }
}
</script>

